<template>
    <div>
        <div class="container" ref="c1">
            <div class="item" v-for="(window, index) in windows" :key="index">
                <div :style="{ top: window.top, left: window.left }"></div>
            </div>
        </div>
        <!-- <div v-show="false">{{ cnt }}</div> -->
        <div>({{ x }},{{ y }})</div>
    </div>
</template>

<script>
export default {
    name: "FunnyWindow",

    data() {
        return {
            windows: [],
            x: 0,
            y: 0,
            size: 120,
            screenSize: { width: 0, height: 0 },
            gridPosition: { left: 0, top: 0 },
        };
    },

    mounted() {
        // 绑定鼠标移动事件
        this.mouseMoveBind();
        // 绑定鼠标滚轮事件
        this.mouseWheelBind();

        // 绑定屏幕宽度事件
        window.onresize = () => {
            return (() => {
                window.screenWidth = document.body.clientWidth;
                this.screenSize.width = window.screenWidth;
                window.screenHeight = document.body.clientHeight;
                this.screenSize.height = window.screenHeight;
                this.gridPosition.left = this.$refs.c1.offsetLeft;
                this.gridPosition.top = this.$refs.c1.offsetTop;
            })();
        };

        for (let i = 0; i < 16; i++) {
            this.windows[i] = {
                top: 0 + "px",
                left: 0 + "px",
            };
        }

        this.x = 60;
        this.y = 60;

        setTimeout(() => {
            window.screenWidth = document.body.clientWidth;
            this.screenSize.width = window.screenWidth;
            window.screenHeight = document.body.clientHeight;
            this.screenSize.height = window.screenHeight;
            this.gridPosition.left = this.$refs.c1.offsetLeft;
            this.gridPosition.top = this.$refs.c1.offsetTop;
        }, 500);
    },

    destroyed() {
        this.mouseMoveDestroyed();
        this.mouseWheelDestroyed();
    },

    watch: {
        x(val) {
            let head = val;
            let rear = val + this.size;
            this.windows.forEach((element, index) => {
                let p = (index % 4) * 80;
                let q = p + 80;
                if (head >= q || rear <= p) element.left = "80px";
                else if (head < p && rear > q) element.left = "0px";
                else if (head >= p && head < q)
                    element.left = (head % 80) + "px";
                else if (rear >= p && rear < q)
                    element.left = "-" + (80 - (rear % 80)) + "px";
            });
        },
        y(val) {
            let head = val;
            let rear = val + this.size;
            this.windows.forEach((element, index) => {
                let p = Math.floor(index / 4) * 80;
                let q = p + 80;
                if (head >= q || rear <= p) element.top = "80px";
                else if (head < p && rear > q) element.top = "0px";
                else if (head >= p && head < q)
                    element.top = (head % 80) + "px";
                else if (rear >= p && rear < q)
                    element.top = "-" + (80 - (rear % 80)) + "px";
            });
        },
    },

    methods: {
        mouseMove(event) {
            let dx =
                (event.screenX -
                    this.screenSize.width / 2 -
                    this.gridPosition.left) /
                3;
            let dy =
                (event.screenY -
                    this.screenSize.height / 2 -
                    this.gridPosition.top) /
                3;
            this.x = Math.round((320 - this.size) / 2 + dx);
            this.y = Math.round((320 - this.size) / 2 + dy);
        },
        mouseMoveDestroyed() {
            document.removeEventListener("mousemove", this.mouseMove);
        },
        mouseMoveBind() {
            document.addEventListener("mousemove", this.mouseMove);
        },
        mouseWheel(event) {
            if (event.deltaY > 0) {
                let a = this.size + 10;
                if (a > 320) a = 320;
                else {
                    this.x -= 5;
                    this.y -= 5;
                }
                this.size = a;
            } else {
                let a = this.size - 10;
                if (a < 80) a = 80;
                else {
                    this.x += 5;
                    this.y += 5;
                }
                this.size = a;
            }
        },
        mouseWheelDestroyed() {
            document.removeEventListener("wheel", this.mouseWheel, true);
        },
        mouseWheelBind() {
            document.addEventListener("wheel", this.mouseWheel, true);
        },
    },
};
</script>

<style scoped>
.show {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(12, 90px);
}
.container {
    justify-content: center;
    justify-items: center;
    align-items: center;
    display: grid;
    grid-template-columns: repeat(4, 80px);
    grid-template-rows: repeat(4, 80px);
}
.item {
    width: 70px;
    height: 70px;
    border-style: solid;
    border-color: black;
    overflow: hidden;
}
.item > div {
    position: relative;
    background: orange;
    width: 70px;
    height: 70px;
}
</style>